
'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { useState, useEffect } from 'react';
import type { User } from '@/types';
import { fetchUserSessionForClient } from '@/app/actions';

import {
  Home,
  ScrollText,
  LayoutDashboard,
  PlusSquare,
  ListChecks,
  Shield,
  FilePenLine,
  Trophy,
  ChevronsLeft,
  ChevronsRight,
  Settings,
  BarChart3, // Added icon for analysis
} from 'lucide-react';
import { cn } from '@/lib/utils';
import {
  Sidebar,
  SidebarContent,
  SidebarFooter,
  SidebarHeader,
  SidebarMenuButton,
  useSidebar,
  Skeleton,
} from '@/components/ui/sidebar';
import UserNavOrLogin from '@/components/layout/UserNavOrLogin';


interface NavItemConfig {
  href: string;
  label: string;
  icon: React.ElementType;
  staffOnly?: boolean;
  dashboardUserOnly?: boolean;
}

const publicNavItems: NavItemConfig[] = [
  { href: '/', label: '首页', icon: Home },
  { href: '/history', label: '历史回顾', icon: ScrollText },
  { href: '/vote-analysis', label: '投票分析', icon: BarChart3 }, // New analysis link
];

const dashboardNavItems: NavItemConfig[] = [
  { href: '/dashboard', label: '仪表盘', icon: LayoutDashboard, dashboardUserOnly: true },
  { href: '/dashboard/submit', label: '提交作品', icon: PlusSquare, dashboardUserOnly: true },
  { href: '/dashboard/my-projects', label: '我的作品', icon: ListChecks, dashboardUserOnly: true },
];

const adminAreaLink: NavItemConfig = {
  href: '/admin', label: '管理后台', icon: Shield, staffOnly: true,
};


export default function UnifiedSidebar() {
  const pathname = usePathname();
  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [loadingSession, setLoadingSession] = useState(true);

  const {
    isMobile,
    state: desktopSidebarState,
    toggleDesktopSidebar,
  } = useSidebar();

  const isActuallyCollapsed = !isMobile && desktopSidebarState === 'collapsed';

  useEffect(() => {
    async function getSession() {
      setLoadingSession(true);
      try {
        const user = await fetchUserSessionForClient();
        setCurrentUser(user);
      } catch (error) {
        console.error("Failed to fetch user session for sidebar:", error);
        setCurrentUser(null);
      } finally {
        setLoadingSession(false);
      }
    }
    getSession();
  }, []);

  let finalNavItems: NavItemConfig[] = [...publicNavItems];

  if (currentUser) {
    // All logged-in users see dashboard links
    finalNavItems.push(...dashboardNavItems);
    if (currentUser.type === 'staff') {
      // Staff users also see the admin area link
      finalNavItems.push(adminAreaLink);
    }
  }
  
  // Remove duplicates - useful if items might overlap conceptually or if logic changes
  finalNavItems = finalNavItems.filter((item, index, self) =>
    index === self.findIndex((t) => (t.href === item.href && t.label === item.label))
  );


  return (
    <Sidebar>
      {!isMobile && (
        <SidebarHeader className="p-3 flex items-center justify-between">
          <Link href="/" className={cn(
              "flex items-center gap-2 transition-opacity duration-300",
              isActuallyCollapsed && "opacity-0 pointer-events-none w-0 h-0"
          )}>
            <svg width="28" height="28" viewBox="0 0 100 100" className="text-primary fill-current shrink-0">
              <rect width="100" height="100" rx="15" fill="hsl(var(--primary))"/>
              <rect x="15" y="15" width="30" height="30" fill="hsl(var(--sidebar-background))"/>
              <rect x="55" y="15" width="30" height="30" fill="hsl(var(--sidebar-background))"/>
              <rect x="15" y="55" width="30" height="30" fill="hsl(var(--sidebar-background))"/>
              <rect x="55" y="55" width="30" height="30" fill="hsl(var(--sidebar-primary))"/>
            </svg>
            <div className="flex flex-col items-start">
              <h1 className="text-xl font-semibold text-sidebar-foreground whitespace-nowrap">VRCub建筑中心</h1>
            </div>
          </Link>
          {!isActuallyCollapsed && (
            <button
              onClick={toggleDesktopSidebar}
              className="text-sidebar-foreground/70 hover:text-sidebar-foreground hover:bg-sidebar-accent h-8 w-8 flex items-center justify-center rounded-md focus-visible:ring-1 focus-visible:ring-sidebar-ring focus-visible:outline-none"
              aria-label="收起侧边栏"
            >
              <ChevronsLeft className="size-5" />
            </button>
          )}
        </SidebarHeader>
      )}
      
      {isMobile && (
        <SidebarHeader className="p-3 border-b border-sidebar-border">
           <h2 className="text-lg font-semibold text-sidebar-foreground">菜单</h2>
        </SidebarHeader>
      )}


      <SidebarContent>
        {loadingSession ? (
          <>
            {[...Array(5)].map((_, i) => (
              <div key={`skeleton-nav-${i}`} className={cn("flex items-center gap-3 rounded-md px-3 py-2.5", isActuallyCollapsed && "justify-center p-2.5")}>
                <Skeleton className="size-5 rounded-sm bg-sidebar-accent/50" />
                {!isActuallyCollapsed && <Skeleton className="h-4 flex-1 max-w-[100px] bg-sidebar-accent/50 rounded" />}
              </div>
            ))}
          </>
        ) : (
          finalNavItems.map((item) => {
            let isActive = false;
            if (item.href === '/') {
              isActive = pathname === '/';
            } else if (item.href === '/admin') {
              isActive = pathname.startsWith('/admin');
            } else if (item.href === '/dashboard') {
              isActive = pathname === '/dashboard' || pathname.startsWith('/dashboard/');
            } else {
              isActive = pathname === item.href || pathname.startsWith(`${item.href}/`);
            }
            
            const isAdminPath = pathname.startsWith('/admin');
            const isAdminViewActive = item.href === '/admin' && isAdminPath;


            return (
              <SidebarMenuButton
                key={`${item.href}-${item.label}`}
                asChild
                isActive={isActive || isAdminViewActive}
                tooltip={item.label}
                className={cn(isActuallyCollapsed ? "justify-center" : "justify-start")}
              >
                <Link href={item.href} className="flex items-center gap-3">
                  <item.icon className={cn("h-5 w-5 shrink-0", (isActive || isAdminViewActive) ? "text-sidebar-primary-foreground" : "text-sidebar-foreground/80 group-hover/button:text-sidebar-accent-foreground")} />
                  {!isActuallyCollapsed && <span className="whitespace-nowrap">{item.label}</span>}
                </Link>
              </SidebarMenuButton>
            );
          })
        )}
      </SidebarContent>

      <SidebarFooter className="p-3">
         {!isMobile && desktopSidebarState === 'collapsed' && (
          <SidebarMenuButton
            onClick={toggleDesktopSidebar}
            tooltip="展开侧边栏"
            className="w-full justify-center mb-2"
            aria-label="展开侧边栏"
          >
            <ChevronsRight className="size-5 text-sidebar-foreground/70 group-hover/button:text-sidebar-accent-foreground" />
          </SidebarMenuButton>
        )}
        <UserNavOrLogin currentUser={currentUser} isSidebarCollapsed={isActuallyCollapsed} />
      </SidebarFooter>
    </Sidebar>
  );
}
