
'use client'; 

import { usePathname } from 'next/navigation';
import { GeistSans } from 'geist/font/sans';
import { GeistMono } from 'geist/font/mono';
import './globals.css';
import { Toaster } from '@/components/ui/toaster';
import UnifiedSidebar from '@/components/layout/UnifiedSidebar';
import { SidebarProvider, SidebarInset, SidebarTrigger } from '@/components/ui/sidebar';
import { PanelLeft } from 'lucide-react';
import { cn } from '@/lib/utils';
import { Button } from '@/components/ui/button';

const geistSans = GeistSans;
const geistMono = GeistMono;

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  const pathname = usePathname();
  
  // Only hide sidebar for auth callback and admin pages
  const isCallbackPage = pathname === '/auth/callback';
  const isAdminPage = pathname.startsWith('/admin');
  const hideSidebarAndMobileHeader = isCallbackPage || isAdminPage;

  return (
    <html lang="zh-CN" className="dark">
      <head>
        <title>VRCub建筑中心</title>
        <meta name="description" content="VRCub建筑中心 - 展示精选的Minecraft建筑作品，并为它们投票。" />
        <link rel="icon" href="/favicon.ico" sizes="any" />
      </head>
      <body className={`${geistSans.variable} ${geistMono.variable} antialiased bg-background text-foreground`}>
        <SidebarProvider defaultOpen={true}>
          <div className="flex min-h-screen">
            {!hideSidebarAndMobileHeader && <UnifiedSidebar />}
            
            <SidebarInset className={cn(
              "flex-1 flex flex-col bg-background overflow-x-hidden",
               isAdminPage && "md:ml-0 group-data-[sidebar-state=expanded]/sidebar-wrapper:md:ml-0" 
            )}>
              {!hideSidebarAndMobileHeader && (
                 <header className="sticky top-0 z-40 h-14 flex items-center gap-4 border-b border-sidebar-border bg-background px-4 md:hidden">
                  <SidebarTrigger variant="outline" size="icon" className="shrink-0">
                    <PanelLeft className="h-5 w-5" />
                    <span className="sr-only">Toggle navigation menu</span>
                  </SidebarTrigger>
                  <h1 className="text-lg font-semibold text-sidebar-foreground">VRCub建筑中心</h1>
                </header>
              )}
              <div className={cn(
                "flex-1 overflow-y-auto", 
                // Apply padding unless it's an admin page or a specific auth page that needs full control
                !isAdminPage && !isCallbackPage && "p-4 sm:p-6 lg:p-8",
                // If it's the login page, allow flex for centering
                pathname === '/login' && "flex" 
              )}>
                {children}
              </div>
            </SidebarInset>
          </div>
        </SidebarProvider>
        <Toaster />
      </body>
    </html>
  );
}
